<template>
  <div id="app">
    <transition :name="transitionName">
      <router-view></router-view>
    </transition>
    <nav-bar></nav-bar>
    <tip v-if="tip"></tip>
  </div>
</template>

<script>
  import NavBar from './components/NavBar'
  import Tip from './components/Tip'
  import {mapState} from 'vuex'

  export default {
    name: 'app',
    data(){
      return {
        transitionName:''
      }
    },
    computed:{
      ...mapState(['tip'])
    },
    components:{
      'nav-bar':NavBar,
      'tip':Tip
    },
    watch: {
      '$route' (to, from) {
        const toDepth = to.path.split('/').length
        const fromDepth = from.path.split('/').length
        this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
      }
    }
  }
</script>

<style>
  html,body {
    height: 100%;
  }
  #app {
    height: 100%;
  }
  [data-dpr='1'] #app {
    font-size: 16px;
  }
  [data-dpr='2'] #app {
    font-size: 32px;
  }
  [data-dpr='3'] #app {
    font-size: 48px;
  }
</style>



